.container{
    max-width: 1200px;
    margin: auto;
    width: 100%;
}

.paragraph{
    color: #667085;
    font-weight: 300;
    font-size: 17px;
    margin: 0;
    text-align:center;
}

.block{
    display: grid;
    padding-top: 27px;
    padding-bottom: 27px;
    min-height: 640px;
    grid-template-columns: 1fr 640px 1fr;
}

.block .content{
    display: flex;
    flex-direction: column;
    align-items: center;
    vertical-align: middle;
    flex-grow: 1;
    padding-top: 173px;
    padding-left: 42px;
    padding-right: 42px;
    min-width: 640px;
}

.block .content h1{
    text-align: center;
}

.ButtonSquare{
    border-radius: 0 !important;
    height: 50px !important;
}

.inputPanel{
    display: grid;
    grid-template-columns: 35% 35% 30%;
    width: 100%;
    padding-top: 40px;
}

@media (max-width: 700px){
    .block .content{
        padding-top: 0;
        padding-left: 0px;
        padding-right: 0px;
        justify-content: center;
    }
    .inputPanel{
        grid-template-columns: 100%;
        gap: 20px;
    }
    .block svg{
        display: none;
    }
}

@media (max-width: 900px ) and (min-width: 710px){
    .block .content{
        padding-left: 0;
        padding-right: 0;
    }
}