.container{
    max-width: 1200px;
    margin: auto;
    width: 100%;
    padding-top:30px;
}

.capability{
    margin-top: 91px;
    display: grid;
    grid-template-columns: 50% 50%;
    line-height: 31px;
    gap:50px 0;
}

.paragraph{
    color: #667085;
    font-weight: 300;
    font-size: 17px;
    margin: 0;
}

.capability figure{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
}

.block1{
    padding-left:50px;
    padding-right:70px;
}
.block2{
    padding-right:80px;
}

.block3{
    padding-left:30px;
    padding-right:120px;
}
.block4{
    padding-right:50px;
}

.block5{
    padding-left:50px;
    padding-right:70px;
}
.block6{
    padding-right:80px;
}

.capability h2{
    color: #2970FF;
    font-size: 24px;
    font-weight: 600;
}

h1{
    color: #2970FF;
    font-size: 34px;
    font-weight: 600;
    margin: 0;
}

@media (max-width: 900px) {
    .capability{
        grid-template-columns: 100%;
    }
    
    .block1{
        padding: 0;
        order: 2;
    }
    .block2{
        padding: 0;
        order: 1;
    }
    
    .block3{
        padding: 0;
        order: 3;
    }
    .block4{
        padding: 0;
        order: 4;
    }
    
    .block5{
        padding: 0;
        order: 6;
    }
    .block6{
        padding: 0;
        order: 5;
    }

    .capability figure h2{
        text-align: center;
    }

    .capability figure .paragraph{
        text-align: center;
    }
    
}
