.container{
    max-width: 1200px;
    margin: auto;
    width: 100%;
}

.morda{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 100px;
}

.banner{
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin-top: 245px;
    line-height: 25px;
}

.demoButton{
    margin-top: 50px;
    height: 50px;
}

.section{
    min-height: 1078px;
}

@media (max-width: 900px) {
    .section{
        min-height: 1050px;
    }
    
    .morda{
        margin-top: 50px;
    }

    .banner{
        margin-top: 155px;
    }
}