.shadow {
    box-shadow: 0 0 .5em rgba(0, 0, 0, .5);
}


.container{
    max-width: 1200px;
    margin: auto;
    width: 100%;
}

.navContent{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navBar{
    height: 70px;
    background-color: #FFFFFF;
    padding-left: 20px;
    padding-right: 20px;
    text-align: center;
    align-items: center;
    display: flex;
}

.logo{

}

.buttons{
    display: flex;
    gap: 40px;
}

.link{
    display: flex;
    color: #667085;
    font-size: 16px;
    font-weight: 500;
    align-items:center;
    justify-content:center;
    border-bottom: 2px solid rgba(0,0,0,0);
    text-decoration: none;
}

.link:hover{
    border-bottom: 2px solid #2970FF;
}

header{
    position: sticky;
    top: 0px;
    transition: 1s;
}

.checkbox{
    display: none;
}

.open{
    display: none;
    cursor: pointer;
}

@media (max-width: 900px){
    .open{
        display: block;
    }
    
    .buttons{
        display: none;
    }
    
    .navContent input:checked + .buttons{
        display: flex !important;
        flex-direction: column;
        position: fixed;
        width: 100%;
        left: 0;
        top: 60px;
        padding: 10px 20px;
        background: #FFF;
    }
}