.container{
    max-width: 1200px;
    margin: auto;
    width: 100%;
    padding-top:30px;
}

.efficiency{
    margin-top: 91px;
    display: grid;
    grid-template-columns: 50% 50%;
    line-height: 31px;
    gap:50px 0;
}

.paragraph{
    color: #667085;
    font-weight: 300;
    font-size: 17px;
    margin: 0;
}

.efficiency figure{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
}

.efficiency h2{
    color: #2970FF;
    font-size: 24px;
    font-weight: 600;
}

h1{
    color: #2970FF;
    font-size: 34px;
    font-weight: 600;
    margin: 0;
}

.block{
    display: flex !important;
    flex-direction: row !important;
    align-items: flex-start !important;
    justify-content: flex-start !important;
    gap: 20px;
}

.block .Image{
    display: flex;
    height: 100%;
    padding-top: 2px;
}

.block h3{
    margin: 0;
}

.blockContent{
    padding: 30px 120px;
}

.block .paragraph{
    font-size: 18px;
}

@media (max-width: 900px) {
    .efficiency{
        grid-template-columns: 100%;
    }
}